@import 'utils';
@import 'fonts';
@import 'typography';
@import './blocks/aboutUs.scss';
@import './blocks/button.scss';
@import './blocks/content.scss';
@import './blocks/footer.scss';
@import './blocks/header.scss';
@import './blocks/icon.scss';
@import './blocks/sidebar.scss';
@import './blocks/topbar.scss';
@import './blocks/whatWeBake.scss';

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
  scrollbar-width: none;

  &:has(.sidebar:target) {
    overflow: hidden;
  }
}

body {
  background: $background-color;
  margin: 0;
}

.logo {
  width: 136px;
  height: 32px;

  @include hover(transform, scale(1.1));
}

a {
  text-decoration: none;
}

.grid_container {
  display: grid;
  grid-template-columns: repeat(4, 2fr);
}
